:root{
    --accent: #FD850D;
    --bg : #f3f3f3;
    --text: #0c0c0c;
    --font: 'Secular One', sans-serif;
}



*, ::after, ::before {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
    cursor: none;
}




html, body{
    overflow: hidden;
    width: 100%;
    height:100%;
}

body {
    font-family: var(--font);
    background-color: var(--bg);
    font-display: swap;
}

body::before{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    content: "";
    opacity: 0.05;
    z-index: 1000;
    pointer-events: none;
    background: url(../img/noise.gif);
}



.hover-this {
    transition: all 0.3s ease;
}

.cursor {
    pointer-events: none;
    position: fixed;
    padding: 0.5rem;
    background-color: var(--accent);
    border-radius: 50%;
    transition: transform 0.3s ease;
    z-index: -1;
}

.hover-this:hover ~ .cursor {
    transform: translate(-50%, -50%) scale(8);
}

nav{
    background-color: transparent;
    box-shadow: none !important;
}




nav .brand-logo {
    color: var(--text);
    text-decoration: none;
    font-size: 40px;
    letter-spacing: 2px;
}




span{ 
    font-weight: bold;
    color: var(--accent);
    transition: 0.3s ease;
}

span:hover{ 
    color: var(--text);
}


.main-scroll{
    display: flex;
    justify-content: center;
    align-items: center;
}



.content{
    position: relative;
    width: 100vw;
    height: 100%;
}


canvas { 
    outline: none;
    width: 100%;
    height: 100%;
}

#container { 
    width: 60px;
    height: 100px;
    position: relative; 
}


.content-inner-first{
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateY(40%);
    text-align: center;
}

.content-inner-first h1{
    font-size: 8vw;
    color: var(--text);
}




















